import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

import "./index.css"
import news from "../data/news.json"

import lifeApply from "../data/life-apply.json"
import lifeCloud from "../data/life-cloud.json"
import lifeEntertainment from "../data/life-entertainment.json"
import lifeShop from "../data/life-shop.json"

import codeOperation from "../data/code-operation.json"
import codeCommunity from "../data/code-community.json"
import codeBlog from "../data/code-blog.json"
import codeLib from "../data/code-lib.json"
import codeTeach from "../data/code-teach.json"

import manageMarket from "../data/manage-market.json"
import manageProject from "../data/manage-project.json"

import designUi from "../data/design-ui.json"
import designUed from "../data/design-ued.json"
import designPic from "../data/design-pic.json"

import toolOnline from "../data/tool-online.json"
import toolProfession from "../data/tool-profession.json"
import toolPragmatic from "../data/tool-pragmatic.json"
import toolPlatform from "../data/tool-platform.json"
import toolSite from "../data/tool-site.json"
class IndexPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      news: [],
      codeOperation: [],
      codeCommunity: [],
      codeBlog: [],
      codeLib: [],
      codeTeach: [],
      lifeApply: [],
      lifeCloud: [],
      lifeEntertainment: [],
      lifeShop: [],
      manageMarket: [],
      manageProject: [],
      designUi: [],
      designUed: [],
      designPic: [],
      toolOnline: [],
      toolProfession: [],
      toolPragmatic: [],
      toolPlatform: [],
      toolSite: [],
    }
  }
  componentDidMount() {
    this.setState({
      news,
      codeOperation,
      codeCommunity,
      codeBlog,
      codeLib,
      codeTeach,
      lifeApply,
      lifeCloud,
      lifeEntertainment,
      lifeShop,
      manageMarket,
      manageProject,
      designUi,
      designUed,
      designPic,
      toolOnline,
      toolProfession,
      toolPragmatic,
      toolPlatform,
      toolSite,
    })
  }
  render() {
    const {
      news,
      codeOperation,
      codeCommunity,
      codeBlog,
      codeLib,
      codeTeach,
      lifeApply,
      lifeCloud,
      lifeEntertainment,
      lifeShop,
      manageMarket,
      manageProject,
      designUi,
      designUed,
      designPic,
      toolOnline,
      toolProfession,
      toolPragmatic,
      toolPlatform,
      toolSite,
    } = this.state
    return (
      <Layout>
        <SEO title="首页" />
        <div className="clearfix">
          <div className="sm-col sm-col-12 md-col-12 lg-col-6 p1">
            <div className="news-panel news-panel_access elevation">
              <div className="news-box-top">行业资讯</div>
              <div className="news-box-content flex flex-wrap">
                {news.map((item, index) => (
                  <a key={index} href={item.link} target="_blank" className="news-box-items flex col-6">
                    <div className="news-box-items__bd">
                      <h4 className="news-box-items-link">{item.title}</h4>
                    </div>
                  </a>
                ))}
              </div>
            </div>
          </div>
          <div className="sm-col sm-col-12 md-col-12 lg-col-6 p1">
            <div className="tabs-box elevation">
              <input className="tabs-radio" type="radio" title="tab-1" name="tabs-state" id="tab-1" defaultChecked />
              <input className="tabs-radio" type="radio" title="tab-2" name="tabs-state" id="tab-2" />
              <input className="tabs-radio" type="radio" title="tab-3" name="tabs-state" id="tab-3" />
              <input className="tabs-radio" type="radio" title="tab-4" name="tabs-state" id="tab-4" />
              <input className="tabs-radio" type="radio" title="tab-5" name="tabs-state" id="tab-5" />
              <div className="tabs flex-tabs">
                <label htmlFor="tab-1" id="tab-1-label" className="tab">
                  编码教程
                </label>
                <label htmlFor="tab-2" id="tab-2-label" className="tab">
                  开源库/框架
                </label>
                <label htmlFor="tab-3" id="tab-3-label" className="tab">
                  社区
                </label>
                <label htmlFor="tab-4" id="tab-4-label" className="tab">
                  博客
                </label>
                <label htmlFor="tab-5" id="tab-5-label" className="tab">
                  运维
                </label>
                <div id="tab-1-panel" className="panel active">
                  {codeTeach.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-2-panel" className="panel">
                  {codeLib.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-3-panel" className="panel">
                  {codeCommunity.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-4-panel" className="panel">
                  {codeBlog.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-5-panel" className="panel">
                  {codeOperation.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="clearfix ">
          <div className=" sm-col sm-col-12 md-col-12 lg-col-6 p1 flex flex-wrap">
            <div className="flex items-stretch ui-box elevation">
              <div className="col col-4 design-box p1">
                <h3 className="center">产品设计</h3>
                <div className="design-box-items center ">
                  {designUi.map((item, index) => (
                    <a key={index} className="ui-link" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
              </div>
              <div className="col col-4 design-box p1">
                <h3 className=" center">UED</h3>
                <div className="design-box-items center">
                  {designUed.map((item, index) => (
                    <a key={index} className="ui-link" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
              </div>
              <div className="col col-4 design-box p1">
                <h3 className=" center">图库</h3>
                <div className="design-box-items center">
                  {designPic.map((item, index) => (
                    <a key={index} className="ui-link" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
              </div>
            </div>
          </div>
          <div className=" sm-col sm-col-12 md-col-12 lg-col-6 p1">
            <div className="tabs-box elevation">
              <input className="tabs-radio" type="radio" title="tab-2-1" name="tabs-state-2" id="tab-2-1" defaultChecked />
              <input className="tabs-radio" type="radio" title="tab-2-2" name="tabs-state-2" id="tab-2-2" />
              <div className="tabs flex-tabs">
                <label htmlFor="tab-2-1" id="tab-2-1-label" className="tab">
                  产品运营
                </label>
                <label htmlFor="tab-2-2" id="tab-2-2-label" className="tab">
                  项目管理
                </label>
                <div id="tab-2-1-panel" className="panel active">
                  {manageMarket.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-2-2-panel" className="panel">
                  {manageProject.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="clearfix ">
          <div className=" sm-col sm-col-12 md-col-12 lg-col-6 p1">
            <div className="flex flex-wrap life-box">
              <div className="col-6 life-box-items ">
                <div className="life-box-elevation elevation">
                  <div className="life-box-gird center">
                    <h3>网购</h3>
                    <div className="flex flex-wrap">
                      {lifeShop.map((item, index) => {
                        const images = require("../images" + item.icon)
                        return (
                          <a key={index} href={item.link} target="_blank" title={item.title}>
                            <div className="p1">
                              <img src={images} className="icon-link-logo" alt={item.title} />
                            </div>
                          </a>
                        )
                      })}
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-6 life-box-items ">
                <div className="life-box-elevation elevation">
                  <div className="life-box-gird center">
                    <h3>招聘</h3>
                    <div className="flex flex-wrap">
                      {lifeApply.map((item, index) => {
                        const images = require("../images" + item.icon)
                        return (
                          <a key={index} href={item.link} target="_blank" title={item.title}>
                            <div className="p1">
                              <img src={images} className="icon-link-logo" alt={item.title} />
                            </div>
                          </a>
                        )
                      })}
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-6 life-box-items ">
                <div className="life-box-elevation elevation">
                  <div className="life-box-gird center">
                    <h3>云服务</h3>
                    <div className="flex flex-wrap">
                      {lifeCloud.map((item, index) => {
                        const images = require("../images" + item.icon)
                        return (
                          <a key={index} href={item.link} target="_blank" title={item.title}>
                            <div className="p1">
                              <img src={images} className="icon-link-logo" alt={item.title} />
                            </div>
                          </a>
                        )
                      })}
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-6 life-box-items ">
                <div className="life-box-elevation elevation">
                  <div className="life-box-gird center">
                    <h3>娱乐</h3>
                    <div className="flex flex-wrap">
                      {lifeEntertainment.map((item, index) => {
                        const images = require("../images" + item.icon)
                        return (
                          <a key={index} href={item.link} target="_blank" title={item.title}>
                            <div className="p1">
                              <img src={images} className="icon-link-logo" alt={item.title} />
                            </div>
                          </a>
                        )
                      })}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className=" sm-col sm-col-12 md-col-12 lg-col-6 p1">
            <div className="tabs-box elevation">
              <input className="tabs-radio" type="radio" title="tab-3-1" name="tabs-state-3" id="tab-3-1" defaultChecked />
              <input className="tabs-radio" type="radio" title="tab-3-2" name="tabs-state-3" id="tab-3-2" />
              <input className="tabs-radio" type="radio" title="tab-3-3" name="tabs-state-3" id="tab-3-3" />
              <input className="tabs-radio" type="radio" title="tab-3-4" name="tabs-state-3" id="tab-3-4" />
              <input className="tabs-radio" type="radio" title="tab-3-5" name="tabs-state-3" id="tab-3-5" />
              <div className="tabs flex-tabs">
                <label htmlFor="tab-3-1" id="tab-3-1-label" className="tab">
                  在线工具
                </label>
                <label htmlFor="tab-3-2" id="tab-3-2-label" className="tab">
                  专业工具
                </label>
                <label htmlFor="tab-3-3" id="tab-3-3-label" className="tab">
                  实用工具
                </label>
                <label htmlFor="tab-3-4" id="tab-3-4-label" className="tab">
                  开放平台
                </label>
                <label htmlFor="tab-3-5" id="tab-3-5-label" className="tab">
                  极客站点
                </label>
                <div id="tab-3-1-panel" className="panel active">
                  {toolOnline.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-3-2-panel" className="panel">
                  {toolProfession.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-3-3-panel" className="panel">
                  {toolPragmatic.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-3-4-panel" className="panel">
                  {toolPlatform.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
                <div id="tab-3-5-panel" className="panel">
                  {toolSite.map((item, index) => (
                    <a key={index} className="tab-content-btn tab-content-btn-default" href={item.link} target="_blank">
                      {item.title}
                    </a>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    )
  }
}
export default IndexPage
